@import url(https://fonts.googleapis.com/css2?family=Exo:ital,wght@0,900;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container-row {
  display: flex;
}

.container-column {
  align-items: center;
  display: flex;
  flex-direction: column;
}

main {
  flex-grow: 1;
}

body {
  background-color: rgb(235, 235, 224);
}

.botao-categoria {
  list-style: none;
}

.botao-categoria label {
  align-items: center;
  display: flex;
  padding: 10px;
  transition: box-shadow 1s;
}

.botao-categoria label:hover {
  box-shadow: inset 0 0 10px rgb(173, 173, 133);
}

.botao-categoria label:active {
  box-shadow: inset 0 0 10px rgb(122, 122, 82);
}

.quadrado {
  margin-right: 5px;
}

.cabecalho {
  align-items: center;
  background-color: maroon;
  display: flex;
  justify-content: space-between;
  padding: 1em;
}

.cabecalho a {
  text-decoration: none;
}

.titulo-principal {
  color: white;
  font-family: Exo , sans-serif;
  font-size: 4em;
  margin: 0;
  text-shadow: 5px 5px black;
}

.botao-carrinho {
  border: 4px solid brown;
  border-radius: 7px;
  box-sizing: border-box;
  margin: 1px;
  padding: 1em;
  transition: box-shadow 100ms;
}

.botao-carrinho:hover {
  background-color: rgb(150, 0, 0);
  box-shadow: 2px 2px black;
  margin: 0 2px 2px 0;
}

.botao-carrinho:active {
  box-shadow: -2px -2px black;
  margin: 2px 0 0 2px;
}

.botao-carrinho a {
  color: white;
  display: flex;
  flex-wrap: nowrap;
  font-family: Exo , sans-serif;
  font-size: 1.5em;
  font-style: italic;
  font-weight: 500;
}

.campo-busca {
  margin: 2em 0;
  width: 60%;
}

.campo-busca input {
  flex-grow: 1;
}

.campo-busca button {
  align-items: center;
  display: flex;
  margin-left: 1em;
  padding: 0.6em 0.8em;
}

.lupa {
  margin-right: 3px;
}

.secao-produtos {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.card-produto {
  background-color: white;
  border: 1px solid rgb(153, 153, 102);
  border-radius: 10px;
  box-shadow: 0 0 10px 3px rgb(153, 153, 102);
  display: flex;
  flex-flow: column nowrap;
  flex-grow: 1;
  margin: 0.8em;
  padding: 1em;
  transition: 300ms;
  width: 17em;
}

.card-produto:hover {
  border: 1px solid brown;
  box-shadow: 0 0 0 2px brown;
}

.link-produto {
  color: black;
  display: flex;
  flex-flow: column nowrap;
  flex-grow: 1;
  justify-content: space-between;
  text-align: center;
  text-decoration: none;
}

.link-produto img {
  max-height: 250px;
}

.menu-categorias {
  background-color: rgb(153, 153, 102);
  height: 100%;
  margin: 0;
  padding: 0;
  width: -webkit-max-content;
  width: max-content;
}

.caixa-produto {
  margin: 0 auto;
  width: 90%;
}

.caixa-produto h2 {
  font-size: 2em;
  margin: 1.5em;
}

.detalhes-produto {
  align-items: center;
  background-color: white;
  border: 1px solid rgb(153, 153, 102);
  border-radius: 10px;
  box-shadow: 0 0 10px 3px rgb(153, 153, 102);
  padding: 2em;
}

.caixa-reviews {
  margin: 1.5em auto;
}

